import React, { Component } from 'react'
import ResultComponent from './Components/ResultComponent'
import KeypadComponent from './Components/KeypadComponent'

export default class Calculator extends Component {
  constructor(props) {
    super(props)
    this.state = {
     displayValue: '',
    }

    this.handleClick = this.handleClick.bind(this)
    this.calculate = this.calculate.bind(this)
    this.reset = this.reset.bind(this)
}
calculate() {
  if (this.state.displayValue.indexOf('x') >  -1) {
    let split = this.state.displayValue.split('x');
    let result = split.reduce((a,b) => parseInt(a) * parseInt(b))
   this.setState({
     displayValue: result
   })
  } else if (this.state.displayValue.indexOf('%') >  -1) {
    let split = this.state.displayValue.split('%');
    let result = split.reduce((a,b) => parseInt(a) / parseInt(b))
   this.setState({
     displayValue: parseFloat(result)
   })
  } else if(this.state.displayValue.indexOf('+') >  -1){
    let split = this.state.displayValue.split('+');
    let result = split.reduce((a,b) => parseInt(a) + parseInt(b))
   this.setState({
     displayValue: result
   })
  } else if(this.state.displayValue.indexOf('-') >  -1) {
    let split = this.state.displayValue.split('-');
    if(split.indexOf('') > -1){
      split = split.filter(value => value !== '')
      let result = split.reduce((a,b) => (parseInt(a)) + parseInt(b))
      this.setState({
        displayValue: '-' + result
      })
    } else {
      let result = split.reduce((a,b) => (parseInt(a)) - parseInt(b))
      this.setState({
        displayValue: result
      })
    }
  } 
}

reset() {
  this.setState({
    displayValue:  ''
  })
}



handleClick(button){
  if(button === '='){
    this.calculate()
  } else if(button === 'clear'){
    this.reset()
  } else {
    this.setState({
      displayValue: this.state.displayValue + button
    })
  }
}

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-4"></div>
          <div className="col-4"> 
            <h1 className="center">React Calculator</h1>
            <div className="calculator"> 
              <ResultComponent displayValue={this.state.displayValue}/>
              <KeypadComponent handleClick={this.handleClick} />  
            </div>
          </div>
          <div className="col-4"></div>
        </div>
      </div>
    )
  }
}
