import React from 'react'

export default function KeypadComponent( {handleClick} ) {
  return (
    <div>
      <div className="row">
        <div className="col-9">
          <button className="col-4 calculator-controls-input" name="clear" onClick={e => handleClick(e.target.name)}>clear</button>
          <button className="col-4 calculator-controls-input" name="0" onClick={e => handleClick(e.target.name)}>0</button>
          <button className="col-4 calculator-controls-operations" name="=" onClick={e => handleClick(e.target.name)}>=</button>
          
          <button className="col-4 calculator-controls-input" name="7" onClick={e => handleClick(e.target.name)}>7</button>
          <button className="col-4 calculator-controls-input" name="8" onClick={e => handleClick(e.target.name)}>8</button>
          <button className="col-4 calculator-controls-input" name="9" onClick={e => handleClick(e.target.name)}>9</button>
          <button className="col-4 calculator-controls-input" name="4" onClick={e => handleClick(e.target.name)}>4</button>
          <button className="col-4 calculator-controls-input" name="5" onClick={e => handleClick(e.target.name)}>5</button>
          <button className="col-4 calculator-controls-input" name="6" onClick={e => handleClick(e.target.name)}>6</button>
          <button className="col-4 calculator-controls-input" name="1" onClick={e => handleClick(e.target.name)}>1</button>
          <button className="col-4 calculator-controls-input" name="2" onClick={e => handleClick(e.target.name)}>2</button>
          <button className="col-4 calculator-controls-input" name="3" onClick={e => handleClick(e.target.name)} >3</button>
        </div>
        <div className="col-3">
          <button className="col-12 calculator-controls-operations" name="%" onClick={e => handleClick(e.target.name)}>%</button>
          <button className="col-12 calculator-controls-operations" name="-" onClick={e => handleClick(e.target.name)}>-</button>
          <button className="col-12 calculator-controls-operations" name="x" onClick={e => handleClick(e.target.name)}>x</button>
          <button className="col-12 calculator-controls-operations" name="+" onClick={e => handleClick(e.target.name)}>+</button>
        </div>
      </div>
    </div>
  )
}
